*{
  padding:0;
  margin:0;
}
a{
  text-decoration: none;
  color:#0D633D;
}
img{
  width:100%;
}
ul,li,dl,ol{
  list-style: none;
}

/* header */

header{
  height:3rem;
  display:flex;
}
h1{
  width:100px;
  padding-left:1rem;
}

nav{
  width:100%;
}
nav>ul{
  display:flex;
  padding-top:1rem;
  font-size: 1rem;
  font-weight: bold;
  color:#0D633D;
}
nav>ul>li{
  padding-left:3rem;
}
.loginText{
  width:10rem;
  padding-top:1rem;
  font-size: 1rem;
  font-weight: bold;
  color:#0D633D;
}
/* Slide-container */
.slide-container{
  width:100%;
  height:27.56rem;
  overflow: hidden;
  position: relative;
}
.slideBox{
  width:700%;
  height:27.56rem;
  display:flex;
  pointer-events: none;
  position:absolute;
  top:0;
  left:-100%;
}
.active{ transition:.3s ease-in-out;}
.slide-item{
  display: inline-block;
  width:100%;
  height:100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position:0 30%;

}
.slide-item:nth-child(1){
background-image: url(./img/Slide/pic05.jpg);
}
.slide-item:nth-child(2){
background-image: url(./img/Slide/pic01.jpg);
}
.slide-item:nth-child(3){
background-image: url(./img/Slide/pic02.jpg);
}
.slide-item:nth-child(4){
background-image: url(./img/Slide/pic03.jpg);
}
.slide-item:nth-child(5){
background-image: url(./img/Slide/pic04.jpg);
}
.slide-item:nth-child(6){
background-image: url(./img/Slide/pic05.jpg);
}
.slide-item:nth-child(7){
background-image: url(./img/Slide/pic01.jpg);
}
.btnBox{
display: flex;
justify-content:center;
padding-top:.3rem;
}
.arr{
  background-color:#fff;
  border-top:1px;
  border-right:2px;
  border-bottom:2px;
  border-left:1px;
  border-color: #d4d4d4;
  border-style: solid;
  width:20px;
  padding-top:3px;
}
.arr:active{
  border-top:2px;
  border-right:1px;
  border-bottom:1px;
  border-left:2px;
  background-color:#fff;
  border-color: #d4d4d4;
  border-style: solid;
  width:20px;
  padding-top:3px;
}
.page{
  padding:0 .5rem;
}

/*mainText */
.mainText{
  padding-left:15rem;
}
.text01{
  font-size: 2.5rem;
  font-weight: bold;
  display: inline-block;
  line-height:0;
}
.text02{
  font-size: .9rem;
}
/* menu */

.menu{
  margin-top:15rem;
  width:100%;
  height:120px;
  display: flex;
}
.menu>span{
  display: inline-block;
  width:3%;
  height:100%;
  background-color:#0D633D;
}
.menu>ul{
  width:100%;
  height:100%;
  display: flex;
  justify-content: space-evenly;
  padding-top:1rem;
}
.menu>ul>li{
  width:100px;

}
/* evnet */

.event{
  width:80%;
  margin:15rem auto;
}

.more{
  font-size: 0.8rem;
  text-align: right;
}

/* footer */
.footer{
  width:100%;
  height:50px;
  background-color: #0D633D;
  text-align: center;
  color:#fff;
  line-height: 50px;
  position: absolute;
  bottom:0;
}

/* login */

.login{
  width:100%;
  display: flex;
  padding-top:6gitrem;
}
.loginImg{
width:50%;
border-right:1px solid #ccc;
}
.loginImg>dt{
  width:300px;
  height:300px;
  background: no-repeat 0 0 url('./img/Slide/pic02.jpg');
  background-size: cover;
  margin: 0 auto;
}
.loginImg>dd{
  width:150px;
  margin: 1rem auto;
}